


// .mobile.margin(top,)

.mobile\.margin\(x\,auto\){
  margin-left: auto;
  margin-right: auto;
}

.mobile\.margin\(left\,auto\){
  margin-left: auto;
}

.mobile\.margin\(right\,auto\){
  margin-right: auto;
}

@for $i from 0 through 100 {
  .mobile\.margin\(top\,#{$i * 5}\){
    margin-top: #{$i * 5}px;
  }

  .mobile\.margin\(bottom\,#{$i * 5}\){
    margin-bottom: #{$i * 5}px;
  }
}


@for $i from 0 through 20 {

  .mobile\.padding\(all\,#{$i * 5}\){
    padding: #{$i * 5}px;
  }

  .mobile\.padding\(x\,#{$i * 5}\){
    padding-left: #{$i * 5}px;
    padding-right: #{$i * 5}px;
  }
  
  .mobile\.padding\(y\,#{$i * 5}\){
    padding-top: #{$i * 5}px;
    padding-bottom: #{$i * 5}px;
  }

  .mobile\.padding\(top\,#{$i * 5}\){
    padding-top: #{$i * 5}px;
  }

  .mobile\.padding\(bottom\,#{$i * 5}\){
    padding-bottom: #{$i * 5}px;
  }

  .mobile\.padding\(left\,#{$i * 5}\){
    padding-left: #{$i * 5}px;
  }

  .mobile\.padding\(right\,#{$i * 5}\){
    padding-right: #{$i * 5}px;
  }

  .mobile\.margin\(all\,#{$i * 5}\){
    margin: #{$i * 5}px;
  }

  .mobile\.margin\(x\,#{$i * 5}\){
    margin-left: #{$i * 5}px;
    margin-right: #{$i * 5}px;
  }
  
  .mobile\.margin\(y\,#{$i * 5}\){
    margin-top: #{$i * 5}px;
    margin-bottom: #{$i * 5}px;
  }

  .mobile\.margin\(left\,#{$i * 5}\){
    margin-left: #{$i * 5}px;
  }

  .mobile\.margin\(right\,#{$i * 5}\){
    margin-right: #{$i * 5}px;
  }


}